  },

  methods: {
    detectAndChangeLanguage: function () {
      var userLang = navigator.language || navigator.userLanguage; 
      if (/en.*/.test(userLang)) {
        this.$i18n.locale = 'en'
      } else {
        this.$i18n.locale = 'zh'
      }
    }
  },
  mounted() {
    this.detectAndChangeLanguage()
  }
}
